@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  height: 100vh;
  font-size: 16px;
}

.container {
  position: relative;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}

.titles {
  position: fixed;
  font-size: 2em;
  color: #fff;
  z-index: 1000;
  top: 20px;
  left: 20px;
  width: 60%;
  transition: all 0.2s;
}

.titles-animation {
  animation: title 0.7s;
}

.title {
  font-size: 1.5em;
  margin: 0;
}

.sidebar {
  height: 100%;
  width: 60%;
  position: absolute;
  top: 0;
  left: 0;
  transition: transform 0.5s ease-in-out;
}

.sidebar > div {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  color: #fff;
  padding: 0 300px 0 20px;
  line-height: 1.3em;
}

.main-slide {
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  width: 40%;
  transition: transform 0.5s ease-in-out;
}

.main-slide > div {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

button {
  background-color: #fff;
  border: none;
  color: #aaa;
  cursor: pointer;
  font-size: 16px;
  padding: 15px;
}

button:hover {
  color: #222;
}

button:focus {
  outline: none;
}

.container .controls button {
  position: absolute;
  left: 60%;
  top: 50%;
  z-index: 100;
}

.container .controls .down-button {
  transform: translateX(-100%);
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.container .controls .up-button {
  transform: translateY(-100%);
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.img {
  width: 100%;
}

.img1 {
  background: linear-gradient(182deg, #cdd005 4%, #6d4721 145%);
}

.img2 {
  background: linear-gradient(182deg, #958164 4%, #cac8cb 145%);
}
.img3 {
  background: linear-gradient(182deg, #86724f 4%, #b39f8d 145%);
}
.img4 {
  background: linear-gradient(182deg, #958067 4%, #8d5721 145%);
}

.contact {
  position: fixed;
  bottom: 20px;
  left: 20px;
  color: #fff;
  &__img {
    margin-right: 10px;
  }
  &__number {
    color: #fff;
    text-decoration: none;
    margin-left: 10px;
    display: flex;
    align-items: center;
  }
}

.text-active {
  animation: title 1s;
}

.contact-active {
  animation: title 1.2s;
}

@keyframes title {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 375px) {
  .title {
    font-size: 0.7em;
    margin: 0;
  }
  .sidebar > div {
    padding: 0 30px 0 20px;
  }
  .contact {
    font-size: 0.8em;
    left: 5px;
  }
}
